<template>
  <v-container class="pa-0">

 
  <v-card class="my-6">
    <v-card-title>
       <v-icon size="36" class="mx-2">mdi-cash-multiple</v-icon>Retention
      <v-spacer></v-spacer>
      {{ this.$route.query.created_on.split('|').join(' ~ ') }}
    </v-card-title>
    <v-card-text>
      <v-row>
        <v-col sm="4">
          <app-auto-complete ref="user" outlined rounded dense hide-details v-bind="userFilter"
                          :value="setFilterValue($route.query['user'] || null, 'user')"
                          @input="filter('user',$event)"
          ></app-auto-complete>
        </v-col>
        <v-col sm="4">
          <app-date-range outlined rounded dense hide-details
                          :value="setFilterValue($route.query['created_on'] || dates.join('|'), 'created_on')"
                          @input="filter('created_on',$event)"
          ></app-date-range>
        </v-col>
      </v-row>
    </v-card-text>
  </v-card>
    <app-panel title="Total" :count="totals.count" :amount="totals.amount"></app-panel>

  <v-data-table   
      :loading="loading"
      :headers="headers"
      :items="items"
      multi-sort
      class="elevation-1"
      @click:row="goTo"
      hide-default-footer
    >
    <template v-slot:loading   >
      <v-skeleton-loader
          class="mt-6"
          height="94"
          type="list-item-three-line"
        >
      </v-skeleton-loader>
    </template>
    <template v-slot:item.amount="{ item }" >
      {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.amount) }}
    </template>
    <template v-slot:footer>
          <!-- :value="Number($route.query.page) || 1" -->
         <v-pagination
           class="pa-4" 
          :value="Number($route.query.page) || 1"
          @input="page"
          :length="totalPages"
          :total-visible="10"
          circle
        ></v-pagination>
      </template>
  </v-data-table> 
    
  </v-container>
</template>
<script>
import AppAutoComplete from './../components/Autocomplete'
import AppDateRange from '../components/DateRange'
import AppPanel from '../components/dashboard/Panel'
import utility from './../utility'
 
import { mapActions } from 'vuex'
import $ from 'jquery'
import debounce from 'debounce'

export default {
  components: {
    AppAutoComplete,
    AppDateRange,
    AppPanel
  },
  data () {
    return {
      loading: false,
      dates: utility.currentDateRange,
      userFilter: {
        label: "Owner",
        prependInnerIcon: "mdi-account",
        hideDetails: true,
        collection: 'user',
        itemText: "displayName",
        itemValue: "_id",
        hideAddNew: true,
        or: false,
        query: {
           role: ['!wakel', '!affiliate']
        }
      },
      headers: [{
            text: 'Owner',
            align: 'start',
            sortable: false,
            value: 'user.displayName',
        },
        { text: 'Deposits',  sortable: false, value: 'count' },
        { text: 'Amount',  sortable: false, value: 'amount' }],
      items: [],
      totalPages: 0,
      totals: {
        count: 0,
        amount: 0
      }
    }
  },
  watch :{
    '$route': {
      handler: 'updateComponent',
      immediate:true
    } 
  },
  methods: {
    ...mapActions({
      get: 'api/get'
    }),
     async fetchData() {
       
     
      this.items = []
      this.loading = true
      const rtn = await this.get({ collection: 'department', query: { type: 'rtn' } })
      const rtn_id = rtn.data.docs[0]._id 
      this.$refs.user.query.department=rtn_id
      const result = await this.get({ collection: 'retention' + window.location.search, query: { department: rtn_id } })
      const total = await this.get({ collection: 'retention/total' + window.location.search, query: { department: rtn_id } })
        
      
    
      if (total && total.status == 200 && total.data[0]) {
         
        this.totals.count = total.data[0].count
        this.totals.amount = total.data[0].amount
      } else {
        this.totals.count = 0
        this.totals.amount = 0
      }
      
      if (result && result.status == 200) {
        this.items = result.data.docs
        this.totalPages = result.data.totalPages
      }
  
      this.loading = false
    },
    page (page) {
      const  query  = Object.assign({},this.$route.query) // copy the query object to avoid routing to the same route error
      query.page = page
      const uri =  '/retention?' + $.param(query)
      this.$router.push(uri)
      // this.options.page = page
    },
    goTo (item) {
      const created_on = this.$route.query.created_on
      const query = {
        'user': item._id, 
        'department': item.user.department, 
        'created_on': created_on, 
        sortBy: ['created_on'],
        sortDesc: [false]
      }
      
      this.$router.push('/deposit?' + $.param(query))
    },
 
    setFilterValue (value, field) {
      if (field == 'created_on' && value)
        value = value.split('|')

      return value
    },
    filter: debounce(function (field, value) {
        
        if (field == 'created_on' && Array.isArray(value))
          value = value.join('|')

 
        const query = Object.assign({},this.$route.query) // copy the query object to avoid routing to the same route error
        if (!value && query[field])
          delete query[field]
        else
          query[field] = value
        
        query.page = 1
  
        const uri =  '/retention?' + $.param(query)
  
        this.$router.replace(uri) 

       
    
         
      
    }, 500),
    updateComponent () {
      if (this.$route.query['created_on'])
        this.fetchData()
      else {
         const uri =  '/retention?' + $.param({
           created_on: this.dates.join('|')
         })
  
        this.$router.replace(uri) 
      }
    }
  } 
}
</script>